    /*
     *   Return Applications, Categories or processes list
     */
	getApplicationCategoriesList: function(params) {
        var self = this;
        var data = {};
        var url = "";
        
        /*userId and filterCaseSecurity params are used to filter by user and case security active in categories or process list*/ 
        if(params.userId){
            data.userId = params.userId;
        }
        if(params.action == "Applications" && params.userId) {
            var url = self.serviceLocator.getUrl("admin-getCaseSecurityApplicationList");
            return $.read(url, data);
        }else if(params.action == "Applications" && !params.userId) {
            url = "admin-getApplicationList";
            return $.ajax({
                url: self.serviceLocator.getUrl(url),
                data: data,
                type: "GET",
                dataType: "json"
            });
        } else if(params.action == "getCategories" && params.userId){
            var url = self.serviceLocator.getUrl("admin-getCaseSecurityCategoriesList");
            data.idApp = params.idApp;
            data.idCategory = params.idCategory;
            if (params.authorizationContext) {
                data.authorizationContext = params.authorizationContext;
            }
			if(data.idCategory == "") {
                data.idCategory = -1;
            }
			if(typeof ( params.filterStartEvent ) !== 'undefined') {
                data.filterStartEvent = params.filterStartEvent;
            }
            return $.read(url, data);
        }else if(params.action == "getCategories" && !params.userId){
            url = "admin-getCategoriesList";
            data.idApp = params.idApp;
            data.idCategory = params.idCategory;
            if (params.authorizationContext) {
                data.authorizationContext = params.authorizationContext;
            }
			if(data.idCategory == "") {
                data.idCategory = -1;
            }
			if(typeof ( params.filterStartEvent ) !== 'undefined') {
                data.filterStartEvent = params.filterStartEvent;
            }
            return $.ajax({
                url: self.serviceLocator.getUrl(url),
                data: data,
                type: "GET",
                dataType: "json"
            });
        }

        // Call ajax and returns promise

    },

